.is-hidden {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.search-form {
  background: linear-gradient(to left, #1025c4, #ffd400);
  justify-content: center;
  align-items: center;
  padding: 20px 0;
  display: flex;
}

.search-form input[type="text"] {
  border: none;
  border-radius: 5px 0 0 5px;
  padding: 10px;
  font-size: 16px;
}

.search-form button[type="submit"] {
  color: #00f;
  cursor: pointer;
  background-color: #ff0;
  border: none;
  border-radius: 0 5px 5px 0;
  padding: 10px;
  font-size: 16px;
  font-weight: bold;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #0056b3;
  border: none;
  border-radius: 5px;
  margin: 0 auto 60px;
  padding: 10px 30px;
  font-size: 16px;
  font-weight: bold;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.search-form button[type="submit"]:hover, .search-form button[type="submit"]:focus {
  background-color: #4caf50;
}

button:hover, button:focus {
  background-color: #4caf50;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin: 40px 50px;
  display: flex;
}

a {
  border-width: 1px;
  border-color: gray;
  border-radius: 20px;
  flex-basis: calc(25% - 20px);
  justify-content: center;
  text-decoration: none;
  display: block;
}

.img-item {
  width: 100%;
  max-width: 100%;
  object-fit: cover;
  height: 250px;
  border-radius: 5px 5px 0 0;
  justify-content: center;
  display: flex;
}

.info {
  color: #fff;
  background-color: #3d5f84;
  border-radius: 0 0 5px 5px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

.info-item {
  text-align: center;
  flex-direction: column;
  line-height: 1.4;
  display: flex;
}

